<template>
  <div class="con">
    <publicTop></publicTop>
    <!-- 头部 -->
    <div class="zhao-top">
      <div
        v-for="(item, index) in tabs"
        :key="index"
        class="zhao-top-li"
        :style="`background-image: url(${url});background-size: 100% 100%;`"
        v-if="item.name == '本周招聘信息'"
        @click="goU(item.name)"
      >
        <div class="zhao-top-li-text1">{{ numObject.recruitmentinfo1 }}</div>
        <div class="zhao-top-li-text2">{{ item.name }}</div>
      </div>
      <div
        v-for="(item, index) in tabs"
        :key="index"
        class="zhao-top-li"
        :style="`background-image: url(${url1});background-size: 100% 100%;`"
        v-if="item.name == '本周实习信息'"
        @click="goU(item.name)"
      >
        <div class="zhao-top-li-text1">{{ numObject.recruitmentinfo2 }}</div>
        <div class="zhao-top-li-text2">{{ item.name }}</div>
      </div>
    </div>
    <!-- 日历 -->
    <Calendar></Calendar>
    <!-- 底部列表 -->
    <div class="home-tabs">
      <div class="home-tabs-scroll">
        <div
          v-for="(item, index) in tabList"
          :key="index"
          class="home-tabs-li"
          :class="ir == index ? 'home-tabs-li-active' : ''"
          @click="goTab(index)"
        >
          <div class="home-tabs-li-text">{{ item.name }}</div>
        </div>
      </div>
    </div>

    <div class="list-all" v-if="subNewsArray.length != 0">
      <div
        v-for="(item, index) in subNewsArray"
        :key="index"
        class="list-all-li"
        @click="goDetail(item.id)"
      >
        <img
          src="@/assets/status-up.png"
          alt=""
          class="list2-all-li-status"
          v-if="
            jjType == 'recruitmentFair' &&
            item.fairType != 1 &&
            item.fairType != 2
          "
        />
        <img
          src="@/assets/status-down.png"
          alt=""
          class="list2-all-li-status"
          v-if="
            jjType == 'recruitmentFair' &&
            (item.fairType == 1 || item.fairType == 2)
          "
        />
        <div
          class="list-all-li-ab1"
          v-if="jjType == 'recruitmentinfo1' || jjType == 'recruitmentinfo2'"
        >
          {{ item.startTime }}
        </div>
        <div
          class="list-all-li-l"
          v-if="jjType == 'recruitmentinfo1' || jjType == 'recruitmentinfo2'"
        >
          <img
            :src="item.corporationinfo.logoUrl"
            alt=""
            v-if="item.corporationinfo.logoUrl"
          />
          <img src="@/assets/momo.png" alt="" v-else />
        </div>
        <div
          class="list-all-li-r"
          v-if="jjType == 'recruitmentinfo1' || jjType == 'recruitmentinfo2'"
        >
          <div class="list-all-li-r-t">
            {{ item.title }}
          </div>
          <div class="list-all-li-r-m">
            <div class="list-all-li-r-m-r" :style="`color:${bgc}`">
              {{ item.corporationinfo.name }}
            </div>
          </div>
          <div
            class="list-all-li-r-r"
            v-if="item.positionNum != 0 || item.positionNum != ''"
          >
            {{ item.positionNum }}个职位 | {{ item.education }}
          </div>
          <div v-else class="list-all-li-r-r">见详情页</div>
        </div>

        <img
          src="@/assets/status-up.png"
          alt=""
          class="list2-all-li-status"
          v-if="jjType == 'bilateralchosefair' && item.fairType != 1"
        />
        <img
          src="@/assets/status-down.png"
          alt=""
          class="list2-all-li-status"
          v-if="jjType == 'bilateralchosefair' && item.fairType == 1"
        />
        <div class="list2-all-li-r" v-if="jjType == 'bilateralchosefair'">
          <div class="list2-all-li-r-t">
            <div class="list2-all-li-r-t-text">
              {{ item.title }}
            </div>
          </div>
          <div class="list2-all-li-r-m">
            <img src="@/assets/gray.png" alt="" class="list2-all-li-r-m-l" />
            <div class="list2-all-li-r-m-r">{{ item.place }}</div>
          </div>
          <div class="list2-all-li-r-r">
            <img src="@/assets/timehei.png" alt="" />
            <div class="list2-all-li-r-r-text">{{ item.startTime }}</div>
          </div>
        </div>

        <div class="list3-all-li-l" v-if="jjType == 'recruitmentFair'">
          <img
            :src="item.corporationinfo.logoUrl"
            alt=""
            v-if="item.corporationinfo.logoUrl"
          />
          <img src="@/assets/momo.png" alt="" v-else />
        </div>
        <div class="list3-all-li-r" v-if="jjType == 'recruitmentFair'">
          <div class="list3-all-li-r-t">
            {{ item.title }}
          </div>
          <div class="list3-all-li-r-m">
            <img src="@/assets/gray.png" alt="" class="list3-all-li-r-m-l" />
            <div class="list3-all-li-r-m-r">{{ item.place }}</div>
          </div>
          <div class="list3-all-li-r-r">
            <img src="@/assets/timehei.png" alt="" />
            <div class="list3-all-li-r-r-text">{{ item.startTime }}</div>
          </div>
        </div>
      </div>
    </div>

    <div class="more-text" v-if="subNewsArray.length != 0">
      <img src="@/assets/btn-more.png" alt="" @click="goTo" />
    </div>

    <HomeTop :i="checkIndex" />
  </div>
</template>

<script>
import HomeTop from "@/components/homeTop.vue";
import Calendar from "@/components/calendar.vue";
import publicTop from "@/components/publicTop.vue";
import qs from "qs";
export default {
  name: "zActivity",
  components: {
    HomeTop,
    Calendar,
    publicTop,
  },
  data() {
    return {
      checkIndex: 2,
      tabList: [],
      inputContent: "",
      tabs: [{ name: "本周招聘信息" }, { name: "本周实习信息" }],
      url: require("../assets/lan.png"),
      url1: require("../assets/purple.png"),
      bgc: this.GLOBAL.bgc,
      subNewsArray: [],
      jjType: "",
      paramY: "",
      ir: 0,
      scroll: 0,
      numObject: {},
    };
  },
  // beforeRouteEnter(to, from, next) {
  //   console.log(to, "to", from, "from");
  //   if (
  //     from.path == "/zhaoDetail" ||
  //     from.path == "/xuanDetail" ||
  //     from.path == "/shuangDetail" ||
  //     from.path == "/Calendar"
  //   ) {
  //     to.meta.keepAlive = true;
  //   } else {
  //     to.meta.keepAlive = false;
  //   }
  //   next();
  // },
  // activated() {
  //   console.log("acoooooooooooooooooooooooooooooo");
  // },
  beforeRouteLeave(to, from, next) {
    if (
      to.path == "/recruitmentinfoForm" ||
      to.path == "/recruitmentFairForm" ||
      to.path == "/bilateralchosefairForm" ||
      to.path == "/Calendar"
    ) {
      this.$store.commit("GET_CATCHE_COMPONENTS", ["zActivity"]);
    } else {
      this.$store.commit("GET_CATCHE_COMPONENTS", []);
    }
    next();
  },
  methods: {
    goTab(index) {
      this.ir = index;
      for (var j = 0; j < this.tabList.length; j++) {
        if (index == j) {
          this.jjType = this.tabList[j].type;
          // this.subNewsArray = this.tabList[j].list;

          if (this.tabList[j].list.length >= 5) {
            this.subNewsArray = this.tabList[j].list.slice(0, 5);
          } else {
            this.subNewsArray = this.tabList[j].list;
          }
        }
      }
    },
    goTo() {
      if (
        this.jjType == "recruitmentinfo1" ||
        this.jjType == "recruitmentinfo2"
      ) {
        // this.$router.push({ name: "zhaoMore" });
        if (this.jjType == "recruitmentinfo1") {
          this.paramY = 0;
          this.$router.push({
            path: "/recruitmentinfoList",
            query: { type: 1 },
          });
        } else {
          this.paramY = 1;
          this.$router.push({
            path: "/recruitmentinfoList",
            query: { type: 2 },
          });
        }
        this.$store.commit("updataTypeText", this.paramY);
      } else if (this.jjType == "recruitmentFair") {
        // this.$router.push({ name: "xuanMore" });
        this.$router.push({ name: "recruitmentFairList" });
      } else {
        // this.$router.push({ name: "shuangMore" });
        // this.$router.push({ name: "bilateralchosefairList" });
        this.$router.push({
          path: "/bilateralchosefairList",
          query: { type: 4 },
        });
      }
    },
    goDetail(id) {
      console.log(id, this.jjType);
      // this.$store.commit("updataDetailId", id);
      if (
        this.jjType == "recruitmentinfo1" ||
        this.jjType == "recruitmentinfo2"
      ) {
        // this.$router.push({ name: "zhaoDetail" });
        this.$router.push({
          path: "/recruitmentinfoForm",
          query: { type: "", positionDetailId: id },
        });
      } else if (this.jjType == "recruitmentFair") {
        // this.$router.push({ name: "xuanDetail" });
        this.$router.push({
          path: "/recruitmentFairForm",
          query: { type: 3, id: id },
        });
      } else {
        // this.$router.push({ name: "shuangDetail" });
        this.$router.push({
          path: "/bilateralchosefairForm",
          query: { type: 4, id: id },
        });
      }
    },
    goU(name) {
      console.log(name);
      if (name == "本周招聘信息") {
        // this.$router.push({ name: "zhaoMore" });
        this.$router.push({
          path: "/recruitmentinfoList",
          query: { type: 1 },
        });
        this.$store.commit("updataTypeText", 0);
      } else {
        // this.$router.push({ name: "zhaoMore" });
        this.$router.push({
          path: "/recruitmentinfoList",
          query: { type: 2 },
        });
        this.$store.commit("updataTypeText", 1);
      }
    },
    getLists() {
      const params = {
        type: "mobile",
        num: 9,
      };
      console.log(params);
      this.$axios
        // .get(
        //   "/f/ajaxHome/ajax_recruitmentActivityList?type=" + "mobile" + "&num=9"
        // )
        .post("/f/ajaxHome/ajax_recruitmentActivityList", qs.stringify(params))
        .then((res) => {
          console.log(res, "数据list");
          if (res.data.state == 1) {
            if (res.data.data && res.data.data.length != 0) {
              this.jjType = res.data.data[0].type;
              for (var j = 0; j < res.data.data.length; j++) {
                if (
                  res.data.data[j].list &&
                  res.data.data[j].list.length != 0
                ) {
                  for (var w = 0; w < res.data.data[j].list.length; w++) {
                    res.data.data[j].list[w].startTime = res.data.data[j].list[
                      w
                    ].startTime.slice(0, 10);
                  }
                }
              }
              this.tabList = res.data.data;

              if (res.data.data[0].list.length >= 5) {
                this.subNewsArray = res.data.data[0].list.slice(0, 5);
              } else {
                this.subNewsArray = res.data.data[0].list;
              }

              // this.subNewsArray = res.data.data[0].list;
            }
          } else {
            this.tabList = [];
            this.subNewsArray = [];
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
    getListData() {
      const params1 = {
        num: 7,
      };
      console.log(params1);
      this.$axios
        // .get("/f/ajaxHome/ajax_getRecruitmentsNum?num=7")
        .post("/f/ajaxHome/ajax_getRecruitmentsNum", qs.stringify(params1))
        .then((res) => {
          console.log(res, "本周");
          if (res.data.state == 1) {
            this.numObject = res.data.data;
          } else {
            this.numObject = {};
          }
        })
        .catch((err) => {
          console.log("获取失败");
        });
    },
  },
  created() {
    console.log(this.$store.getters.getCOMPONENTS, "ffff9");
  },
  mounted() {
    this.getLists();
    this.getListData();
  },
  beforeDestory() {},
  destroyed() {},
};
</script>
